* {
	margin: 0;
	padding: 0;
	box-sizing: inherit;
}

html {
	font-size: 62.5%;
	box-sizing: border-box;
}

body {
	font-family: 'Nunito', sans-serif;
	font-weight: 400;
	height: 100vh;
	color: #333;
	background-image: linear-gradient(to top left, #753682 0%, #bf2e34 100%);
	display: flex;
	align-items: center;
	justify-content: center;
}
